import {
    mapState,
    mapMutations
} from 'vuex'
import Search from "@/components/search.vue";
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
export default {
	data() {
		return {
			// toggle: true,
			pageNum: 1,
			productData: [],
			loadding: false,
			pullUpOn: true,
			notFound: false,
			canReachBottom: true,
			searchData: {
				pageNum: 1,
				pageSize: 10,
				totalPage: null,
				keyword: uni.getStorageSync('searchKeyword') || '',
				orderType: null,
				isDesc: false
			},
			scrollTop: 0,
			old: {
				scrollTop: 0
			}
		}
	},
	methods: {
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		linkToDetail(e){
			uni.navigateTo({
				url: '/pages/productDetail/productDetail?id=' + e
			});
		},
		// goTop: function() {
		// 	// this.toggle = false;
		// 	uni.pageScrollTo({
		// 		scrollTop: 0,
		// 		// duration: 5
		// 	})
		// 	// setTimeout(() => {
		// 	// 	this.toggle = true
		// 	// }, 10)
		// },
		doQuery(orderType){
			this.goTop();
			this.searchData.pageNum=1;
			switch (orderType){
				case 2:
					this.searchData.isDesc=!this.searchData.isDesc;
					break;
				case 3:
					this.searchData.isDesc=!this.searchData.isDesc;
					break;
				default:
					this.searchData.isDesc=false;
			}
			this.doSearch(orderType);
			uni.setStorageSync('product_searchType_storage', orderType);
		},
		doSearch(orderType){
			this.loadding = true;
			this.pullUpOn = true;
			this.searchData.orderType=orderType || 0;
			
			this.$http.post('/mall/app/goods/list', {
				accountId: this.userInfo && this.userInfo.accountId || '',
				...this.baseInfo,
				...this.searchData
			})
			.then( res => {
				// console.log(res)
				if(res.code == 0){
					if(res.result){
						this.searchData.totalPage=res.result.totalPage;
						if(res.result.mallGoodsList.length == 0){
							// this.notFound=true;
							
							this.loadding = false;
							this.pullUpOn = false;
						}else{
							this.loadding = false;
						}
						if(this.searchData.pageNum == 1){
							if(res.result.mallGoodsList){
								this.productData=res.result.mallGoodsList;
							}
						}else{
							this.productData=this.productData.concat(res.result.mallGoodsList);
						}
					}
					
				}else{
					console.log("product.vue-- list获取数据列表失败");
				}
			})
			.catch( err => {
				console.log("product.vue-- list获取数据列表错误");
			})
		},
		initSearch(){
			// let keyword = uni.getStorageSync('searchKeyword') || '';
			// this.searchData.keyword=keyword;
			this.searchData.pageNum=1;
			// this.notFound=false;
			// this.canReachBottom=true;
			// this.pullUpOn=true;
			this.productData=[];
		}
		// initProductData(){
		// 	this.doSearch();
		// 	// this.$http.post('/mall/app/goods/list', {
		// 	// 	...this.baseInfo,
		// 	// 	pageNum: this.pageNum
		// 	// })
		// 	// .then( res => {
		// 	// 	if(res.code == 0){
		// 	// 		this.productData=res.result.mallGoodsList
		// 	// 	}else{
		// 	// 		console.log("product.vue-- list获取数据列表失败");
		// 	// 	}
		// 	// 	console.log(res)
		// 	// })
		// 	// .catch( err => {
		// 	// 	console.log("product.vue-- list获取数据列表错误");
		// 	// })
		// }
	},
	computed: {
		...mapState("common", ['baseInfo', 'userInfo']),
	},
	onLoad: function(){
		
	},
	onShow(){
		let keyword = uni.getStorageSync('searchKeyword') || '';
		this.searchData.keyword=keyword;
		this.initSearch();
		this.doSearch(0);
	},
	onHide(){
		uni.removeStorageSync('searchKeyword');
		uni.removeStorageSync('product_searchType_storage');
	},
	// onShow(){  
	//     // 监听事件  
	//     uni.$on('handleSearchKeywor',(keyword)=>{  
	//         this.searchData.keyword = keyword;  
	// 		console.log(this.searchData.keyword)
	//     })  
	// },  
	// onHide() {  
	//     // 移除监听事件  
	// 	uni.$off('handleSearchKeywor');  
	// }, 
	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {
		//延时为了看效果
		// setTimeout(() => {
		// 	this.productList = this.loadData;
		// 	this.pageNum = 1;
		// 	this.pullUpOn = true;
		// 	this.loadding = false;
		// 	uni.stopPullDownRefresh();
		// 	this.tui.toast("刷新成功")
		// }, 200)
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.searchData.pageNum++;
		// if(this.canReachBottom){
		if (this.searchData.pageNum == this.searchData.totalPage) {
			this.loadding = false;
			this.pullUpOn = false;
		}
		// }else{
		// 	this.loadding = false;
		// }
		
		this.doSearch(uni.getStorageSync('product_searchType_storage'));
	},
	components: {
		Search,
		tuiLoadmore,
		tuiNomore
	}
}
